.ModalConductor {
    display: 'flex';
    -ms-flex-align: 'center';
        align-items: 'center';
    -ms-flex-line-pack: center;
        align-content: center;
    -ms-flex-pack: 'center';
        justify-content: 'center';
    position: 'fixed';
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    padding: '10%';
    background-color: 'rgba(0, 0, 0, 1.0)';
    z-index: '9999';
    width: 100vw;
    max-height: calc(100vh - 200px);
    overflow-y: auto;
    
}


